.badge {
	padding: 5px 10px;
	background: #eeeeee;
	color: #22313f;
	border-radius: 5px;
	margin: 0px 5px;
	box-shadow: 0px 5px 10px 0px rgba(238, 238, 238, 0.3);
	display: flex;
	align-items: center;
	& > p {
		font-size: 0.8rem;
		font-weight: 700;
	}
	&:first-child {
		margin-left: 0;
	}
	&:last-child {
		margin-right: 0;
	}
}
