.inputText_container {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 30px;
	& > * {
		width: 100%;
	}
	label {
		display: flex;
		font-size: 19px;
		font-weight: 600;
		margin-bottom: 5px;
	}
	.inputText_input {
		display: flex;
		border-radius: 6px;
		padding: 15px;
		border: 1px solid #cccccc;
		min-width: 240px;
	}
}
