.modal_container {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	.shadow_back {
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.6);
	}
	.modal {
		position: absolute;
		width: 80%;
		height: 80%;
		padding: 20px;
		left: 10%;
		top: 10%;
		background: white;
		border-radius: 8px;
	}
}
