html {
	font-size: 16px;
	color: #22313f;
	body {
		max-width: 100vw;
		overflow-x: hidden;
		p {
			font-size: 16px;
		}
	}
}

h1 {
	font-size: 5rem;
	font-family: 'Anton', sans-serif;
}
h2 {
	font-size: 3.5rem;
	font-family: 'Anton', sans-serif;
}
h3 {
	font-size: 2.5rem;
	font-family: 'Anton', sans-serif;
}
h4 {
	font-size: 1.8rem;
	font-family: 'Anton', sans-serif;
}
.horizontal_list {
	display: flex;
	flex-direction: row;
}
.vertical_list {
}
.page {
	min-height: 100vh;
	min-width: calc(100vw - (30px / 2));
	background: #eeeeee;
}
