.vignet {
	background-color: #bfc7df;
	display: flex;
	flex-wrap: wrap;
	border-radius: 10px;
	margin: 15px 5px;
	padding: 30px;
	box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.1);
	& > * {
		width: 100%;
	}
	.location {
		display: flex;
		align-items: center;
		.game_location_name {
			margin-left: 20px;
			font-size: 1.5rem;
			font-weight: 600;
		}
	}
	.vignet_bets_container {
		margin-top: 20px;
		.vignet_bets_list {
			.vignet_bet {
				width: 50%;
			}
		}
	}
}

.vignet.next_game {
}
.vignet.past_game {
	width: 75vw;
	min-width: 75vw;
}
.vignet.future_game {
	width: 75vw;
	min-width: 75vw;
}
