.row {
	display: flex;
	flex-wrap: wrap;
	& > [class*='col-'] {
		@include media-min-max('phone', 'tablet') {
			width: 100%;
			min-width: 100%;
			max-width: 100%;
			margin-top: 15px;
			&:first-child {
				margin-top: 0;
			}
		}
	}
}
