.btn {
	border-radius: 6px;
	border: none;
	transition: 0.2s;
	background: #22313f;
	color: #ffffff;
	font-size: 16px;
	padding: 10px 35px;
	box-shadow: 0px 8px 12px 0px rgba(34, 49, 63, 0.3);
	cursor: pointer;
	&:hover {
		transition: 0.2s;
		background: #2c4153;
	}
	&.btn_fullsize {
		height: 100%;
		width: 100%;
	}
}
